.grid {
  display: grid;
  grid-template-columns: 10% 1fr 10%;
  grid-template-rows: 30px 1fr;
  /* grid-column-gap: 10px; */
  user-Select: none;
}

.drawAreaStyle:focus {
  outline:none;
}

.transformControl:hover {
  stroke: purple;
  stroke-width: 5px;
}

.controlArrow {
  fill: grey;
  stroke: none;
}

.rotateArrow {
  fill: none;
  stroke-width: 3px;
  stroke: grey;
  stroke-linejoin: round;
  stroke-linecap: round;
}

.unmetConstraints {
  position: absolute;
  bottom: 0;
}

.fileBar {
  grid-column: 2;
  grid-row: 1;
  /* background: #ff6767; */
  display:-ms-flexbox;
  display:flex;
  border: 2px solid black;
  padding: 3px;
}

.vl {
    border-left: 3px solid black;
    height: 100%;
    margin-left: 10px;
    margin-right: 10px;
}

.drawAreaStyle {
  border: 1px solid black;
  outline-color: black;
  grid-column: 2;
  grid-row: 2;
  border-style: solid;
  margin-left: 20;
  display: block;
  height: 95vh - 30px;
}

.constraints {
  background: white;
  border: 2px solid black;
  padding: 3px;
  grid-column: 1;
  grid-row-start: 1;
  grid-row-end: 2;
  position: -webkit-sticky;
  position: sticky;
  left: 0;
  cursor: default;
  height: 95vh;
  overflow-y: scroll;
}

.constraint:hover {
  color: grey;
}

.constraints .x {
  float: right;
  margin-right: 30;
  color: orange;
  font-weight: bold;
}

.constraints .x:hover {
  color: red;
}

.sidebar {
  background: white;
  border: 2px solid black;
  padding: 3px;
  grid-column: 3;
  grid-row-start: 1;
  grid-row-end: span 2;
  cursor: default;
  position: -webkit-sticky;
  position: sticky;
  right: 0;
  height: 95vh;
  overflow-y: scroll;
}

.tools:hover {
  color: grey;
}

em {
  text-decoration: underline;
}



